* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* div {
    width: 100%;
    height: 100vh;
} */

#name {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: fill 0.5s ease forwards 4s;
}

.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    color: white;
    font-size: 50px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}